"use client"

import { useAuth } from "@/lib/auth"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Container } from "@/components/layout/container"
import { Package, CreditCard, Settings, Users } from "lucide-react"
import { OnboardingWizard, useOnboarding } from "@/components/saas/OnboardingWizard"
import { StatusPill } from "@/components/saas/StatusPill"
import { SaaSTopBar } from "@/components/saas/SaaSTopBar"

import { SEED_DATA } from "@/data/nuvioData"

export default function DashboardPage() {
    const { session, logout } = useAuth()
    const { showOnboarding, completeOnboarding } = useOnboarding()

    const tenantId = session?.tenantId || "acme"
    const data = SEED_DATA[tenantId] || SEED_DATA.acme

    return (
        <>
            {showOnboarding && <OnboardingWizard onComplete={completeOnboarding} />}

            <div className="py-8">
                <Container>
                    <SaaSTopBar title="Dashboard" />

                    {/* In Progress Note */}
                    <div className="mb-6 p-4 rounded-lg bg-muted/30 border border-border/50">
                        <p className="text-sm text-muted-foreground text-center">
                            <span className="font-medium">NuVio Core Hub.</span> System modules are active for <span className="text-foreground font-semibold">{tenantId}</span>.
                        </p>
                    </div>

                    {/* Stats Grid */}
                    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                        <Card>
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">Platform Revenue</CardTitle>
                                <CreditCard className="h-4 w-4 text-muted-foreground" />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">
                                    ${data.invoices.reduce((acc, inv) => acc + inv.amount, 0).toLocaleString()}
                                </div>
                                <p className="text-xs text-muted-foreground">Current billing cycle</p>
                            </CardContent>
                        </Card>
                        <Card>
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">Active Assets</CardTitle>
                                <Package className="h-4 w-4 text-muted-foreground" />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">{data.vehicles.length}</div>
                                <p className="text-xs text-muted-foreground">Fleet modules active</p>
                            </CardContent>
                        </Card>
                        <Card>
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">Customer base</CardTitle>
                                <Users className="h-4 w-4 text-muted-foreground" />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">+{data.customers.length}</div>
                                <p className="text-xs text-muted-foreground">Managed via NuVio Core</p>
                            </CardContent>
                        </Card>
                        <Card>
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">Active Modules</CardTitle>
                                <Settings className="h-4 w-4 text-muted-foreground" />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">{data.enabledModules.length}</div>
                                <p className="text-xs text-muted-foreground">System nodes operational</p>
                            </CardContent>
                        </Card>
                    </div>

                    {/* Modules Section */}
                    <div className="mt-8 space-y-4">
                        <h2 className="text-xl font-semibold tracking-tight">Active Modules</h2>
                        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                            {data.enabledModules.map(module => (
                                <Card key={module} className="border-purple-500/10 hover:border-purple-500/30 transition-colors">
                                    <CardHeader className="p-4 pb-2">
                                        <CardTitle className="text-xs uppercase tracking-widest text-muted-foreground">{module}</CardTitle>
                                    </CardHeader>
                                    <CardContent className="p-4 pt-0">
                                        <div className="text-sm font-bold flex items-center gap-2">
                                            <div className="h-2 w-2 rounded-full bg-green-500" />
                                            Operational
                                        </div>
                                    </CardContent>
                                </Card>
                            ))}
                        </div>
                    </div>

                    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7 mt-8">
                        <Card className="col-span-4">
                            <CardHeader>
                                <CardTitle>Asset Distribution</CardTitle>
                            </CardHeader>
                            <CardContent className="pl-2">
                                <div className="h-[200px] flex flex-col items-center justify-center text-muted-foreground bg-muted/20 rounded-md gap-2">
                                    <p className="text-xs font-mono uppercase tracking-widest">Live Data Stream</p>
                                    <div className="flex gap-1">
                                        {data.vehicles.map((v, i) => (
                                            <div key={i} className="h-4 w-4 rounded-sm bg-purple-500/40" />
                                        ))}
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                        <Card className="col-span-3">
                            <CardHeader>
                                <CardTitle>Recent Activity</CardTitle>
                            </CardHeader>
                            <CardContent>
                                <div className="space-y-4">
                                    {data.invoices.slice(0, 5).map((inv, i) => (
                                        <div key={i} className="flex items-center text-xs">
                                            <div className="h-8 w-8 rounded-full bg-accent/20 flex items-center justify-center font-bold text-accent-foreground">
                                                INV
                                            </div>
                                            <div className="ml-3">
                                                <p className="font-medium text-foreground">Invoice {inv.id}</p>
                                                <p className="text-muted-foreground">{inv.date}</p>
                                            </div>
                                            <div className="ml-auto font-mono font-bold">${inv.amount}</div>
                                        </div>
                                    ))}
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                </Container>
            </div>
        </>
    )
}
